.header {
  position: relative;
  display: flex;
  text-align: center;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 8vh;
  background-color: var(--bg-color-secondary);
  z-index: 1;
  transition: all 0.4s;
}

#logout {
  background: none;
  border: none;
  outline: none;
}
